.tooltip-inner {
    width:250px !important;
    max-width: 250px !important;
}
.glyphicon {
    font-family: 'Glyphicons Halflings' !important;
}
.discPer {
    position: relative;
    background: #ed0000;
    color: white;
    height: 24px;
    width: 30px;
    border-radius: 4px 0 0 4px;
    font-size: 15px;
    line-height: 25px;
    display: inline-block;
    float: left;
    text-align: center;
}
.discPer:after {
    left: 100%;
    border: solid transparent;
    content: " ";
    position: absolute;
    pointer-events: none;
    border-color: rgba(237, 0, 0, 0);
    border-left-color: #ed0000;
    border-width: 12px;
}